@import '../../../variables.scss';

.project {
  margin: 2em 0;
  padding: 1em 2em;
  border-radius: 1em;
  background: $comment;
  max-width: 70em;
}

.project h1 {
  margin-bottom: 0.5em;
  color: $green;
}

.project h2 {
  color: $purple;
  font-size: 2em;
  margin: 0.5em 0.25em;
}

.icon {
  margin: 0.5em;
  font-size: 2em;
}

.links {
  max-width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.fullLinks {
  max-width: 30%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.fullProjectText {
  max-width: 60%;
}

.projectImage {
  visibility: hidden;
}

.fullProjectImage {
  max-height: auto;
  max-width: 30%;
  background-color: beige;
  margin: 2em;
}

.fullProjectBody {
  display: flex;
  justify-content: space-between ;
  align-items: center;
}

.linkStrike {
  text-decoration: line-through;
}

@media (max-width: 790px) {
  .project {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .fullProjectBody {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .fullProjectImage {
    max-height: auto;
    max-width: 60%;
    background-color: beige;
    margin: 1em;
  }
  .fullProjectText {
    max-width: 80%;
  }
  .links {
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}
