@import '../../variables.scss';

.bio {
  max-width: 40em;
}

.name {
  color: $green;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-size: 4em;
}
.nameBlock {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.title {
  font-size: 2em;
}

.portrait {
  background: $selection url(https://i.imgur.com/1TwQI3O.png) no-repeat center;
  background-size: fit;
  height: 24em;
  width: 24em;
  border-radius: 50%;
}

.bioBlock {
  margin: 1em;
  padding: 1em 2em;
}

.bio h1 {
  margin-bottom: 0.5em;
}

.bio li {
  // font-size: 2em;
  margin-bottom: 0.5em;
}

.bio li a {
  font-size: 1em;
}

.bio h2 {
  color: $purple;
  font-size: 1.5em;
  margin-bottom: 1em;
  margin-left: 2em;
}

.bio .jam {
  margin-left: 2em;
  border-radius: 1em;
}
