@import './variables.scss';

.app {
  min-height: 100vh;
  width: 100%;
  background-color: $background;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: $foreground;
}

.content {
  margin: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 90em;
  padding-bottom: 3em;
}

h1 {
  color: $purple;
  font-size: 2.5em;
}

p {
  font-size: 2em;
  margin-bottom: 0.5em;
}

a {
  text-decoration: none;
  color: $cyan;
  font-size: 2em;
  // border-bottom: 1px dashed $cyan;
}

a:hover {
  color: $pink;
}

ul {
  list-style: none;
  font-size: 2em;
}

@media (max-width: 790px) {
  .content {
    flex-direction: column;
  }
}
