@import '../../variables.scss';

.projects {
  max-width: 35em;
  transform: translatey(5em);
}

.projectsHeader {
  font-size: 4em;
}

.projectsTop {
  text-align: center;
}

.projects button,
.fullPageProjects button {
  margin-top: 0.5em;
  background-color: transparent;
  border: none;
  color: $cyan;
  font-size: 2em;
}

.projects button:hover {
  color: $pink;
  cursor: pointer;
}

